import { doLogin } from "../queries";
import { checkResponse } from "../../constants/index";


/* Login */
export const do_login = payload => async dispatch => {
    let response = await doLogin(payload);
    response = checkResponse(response);
    let roleid = response.data.data.user.role_id;
    console.log(response.data.data.user.role_id,'hngbolo');
    if (response.success && roleid === 1 ) {
        dispatch({
            type: "LOGIN_SUCCESSFUL",
            payload: response.data
        });
    
        localStorage.setItem("accessToken", response.data.data.access_token);
        localStorage.setItem("user", JSON.stringify(response.data));
        localStorage.setItem("isLogin", true);
        
    } else {
        dispatch({
            type: "LOGIN_FAILED",
            payload: response.error
        });
        localStorage.clear();
    }
};

